body {
  margin: 0;
  font-family: "Work Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.social-contact {
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-icon-div {
  padding: 6px;
  margin: 6px;
  cursor: pointer;
}

.social-icon {
  height: 50px;
}

@media only screen and (max-width: 720px) {
  .social-icon {
    height: 40px;
  }

  .social-contact {
    padding: 25px 0px;
  }
}

.about {
  padding-bottom: 48px;
  margin-bottom: 80px;
}

.about-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.picture {
  height: 486px;
}

.about-info {
  font-size: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.info-name {
  color: #ff1f5a;
  font-size: 60px;
}

.info-text {
  font-size: 1.25rem;
  margin-top: 25px;
  margin-bottom: 25px;
  letter-spacing: -0.15px;
  color: #5f6368;
}

.resume-button {
  background: #b092e2;
  font-family: inherit;
  padding: 0.6em 1.3em;
  font-weight: 900;
  font-size: 18px;
  border: 3px solid black;
  border-radius: 0.4em;
  box-shadow: 0.1em 0.1em;
}

.resume-button:hover {
  -webkit-transform: translate(-0.05em, -0.05em);
          transform: translate(-0.05em, -0.05em);
  box-shadow: 0.15em 0.15em;
  background-color: #a786df;
}

.resume-button:active {
  -webkit-transform: translate(0.05em, 0.05em);
          transform: translate(0.05em, 0.05em);
  box-shadow: 0.05em 0.05em;
}

@media only screen and (max-width: 720px) {
  .about {
    margin-bottom: 0px;
  }

  .about-top {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .picture {
    height: 350px;
  }

  .info-name {
    display: inline-block;
    font-size: 48px;
    margin-top: 5px;
  }

  .info-text {
    font-size: 20px;
    line-height: 28px;
  }

  .resume {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 25px 0px;
  }
}

@-webkit-keyframes wave {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  10% {
    -webkit-transform: rotate(16deg);
            transform: rotate(16deg);
  }
  20% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
  30% {
    -webkit-transform: rotate(16deg);
            transform: rotate(16deg);
  }
  40% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  50% {
    -webkit-transform: rotate(16deg);
            transform: rotate(16deg);
  }
  60% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

@keyframes wave {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  10% {
    -webkit-transform: rotate(16deg);
            transform: rotate(16deg);
  }
  20% {
    -webkit-transform: rotate(-6deg);
            transform: rotate(-6deg);
  }
  30% {
    -webkit-transform: rotate(16deg);
            transform: rotate(16deg);
  }
  40% {
    -webkit-transform: rotate(-4deg);
            transform: rotate(-4deg);
  }
  50% {
    -webkit-transform: rotate(16deg);
            transform: rotate(16deg);
  }
  60% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
}

.hello-emoji {
  -webkit-animation: wave 2.1s 0.6s infinite;
          animation: wave 2.1s 0.6s infinite;
  -webkit-transform-origin: 75% 75%;
          transform-origin: 75% 75%;
  display: inline-block;
  font-size: 40px;
  margin-right: 6px;
}

.hello-text::after {
  content: "";
  -webkit-animation: wordchange infinite 4s;
          animation: wordchange infinite 4s;
}

@-webkit-keyframes wordchange {
  0% {
    content: "Hello";
    color: #482ff7;
  }
  100% {
    content: "\928\92E\938\94D\924\947";
    color: #4ad66d;
  }
}

@keyframes wordchange {
  0% {
    content: "Hello";
    color: #482ff7;
  }
  100% {
    content: "\928\92E\938\94D\924\947";
    color: #4ad66d;
  }
}

.body {
  padding: 12px;
  padding-bottom: 36px;
}

@media only screen and (max-width: 720px) {
  .body {
    padding: 12px 24px;
  }
}

.separator {
  background-image: linear-gradient(
    to right top,
    #d16ba5,
    #c777b9,
    #ba83ca,
    #aa8fd8,
    #9a9ae1,
    #8aa7ec,
    #79b3f4,
    #69bff8,
    #52cffe,
    #41dfff,
    #46eefa,
    #5ffbf1
  );
  height: 6px;
  width: 40%;
  border-radius: 3px;
  margin: 16px 0px;
}

.contact {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.section-title {
  font-size: 32px;
  font-weight: 700;
}

.contact-container {
  display: flex;
  flex: 1 1;
  justify-content: space-between;
  align-items: baseline;
  margin: 16px 0px;
}

.contact-left {
  max-width: 60%;
  font-size: 25px;
  text-align: center;
}

.download {
  background-color: #8ab8fe;
  padding: 8px 16px;
  font-size: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
}

.download a {
  text-decoration: none;
  color: #000;
  font-weight: 600;
}
.download-icon {
  margin-right: 4px;
}

@media only screen and (max-width: 720px) {
  .contact-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .contact-left {
    max-width: 100%;
    font-size: 20px;
    margin-bottom: 12px;
  }

  .download {
    font-size: 16px;
  }
}

.project-card {
  flex: 1 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 16px 0px;
  padding: 10px;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.project-photo {
  width: 40%;
  border-radius: 10px;
}

.project-info {
  margin-right: 24px;
}

.project-title {
  font-size: 20px;
  font-weight: 600;
}

.project-tags {
  display: flex;
  max-width: 60%;
  flex-wrap: wrap;
}

.tag {
  padding: 4px 8px;
  background-color: #8ab8fe;
  border-radius: 4px;
  margin-right: 6px;
  margin-bottom: 4px;
}

.project-links a {
  text-decoration: none;
  color: #000;
}

.project-links {
  margin-top: 6px;
  display: flex;
}

.link-button {
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-color: #8f8ce7;  */
  background-color: #8ab8fe;
  width: -webkit-max-content;
  width: max-content;
  padding: 8px;
  border-radius: 4px;
  font-size: 16px;
  font-weight: 600;
  margin-right: 6px;
}

.link-button i {
  display: flex;
  align-items: center;
  margin-right: 4px;
}

@media only screen and (max-width: 720px) {
  .project-photo {
    width: 100%;
  }

  .project-card {
    flex-direction: column-reverse;
  }

  .project-info {
    margin-left: 0px;
    margin-top: 12px;
  }
  .project-tags {
    max-width: 100%;
  }
}

.projects {
  display: flex;
  flex-direction: column;
  flex: 1 1;
}

.skill-card {
  width: 100px;
  padding: 8px 16px;
  height: 120px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 6px;
  border-radius: 8px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  transition: all 0.2s ease-out;
}

.skill-card:hover {
  -webkit-transform: translateY(-5%);
          transform: translateY(-5%);
  box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px,
    rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
}

.skill-icon {
  font-size: 80px;
}

.skill-name {
  font-size: 18px;
}

@media only screen and (max-width: 720px) {
  .skill-card {
    width: 100px;
    height: 100px;
  }

  .skill-icon {
    font-size: 60px;
  }
}

.skills {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.skills-section {
  display: flex;
  justify-content: space-between;
  margin: 12px 0px;
}

.skills-section-title {
  font-size: 24px;
  font-weight: 600;
}

.skills-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 70%;
  justify-content: flex-end;
}

@media only screen and (max-width: 720px) {
  .skills-list {
    max-width: 100%;
    justify-content: center;
  }

  .skills-section {
    flex-direction: column;
    align-items: center;
  }

  .skills-section-title {
    margin: 10px 0px;
  }
}

.footer {
  padding: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
}

.header {
  display: flex;
  justify-content: space-between;
  flex: 1 1;
  padding: 12px;
  padding-top: 30px;
  position: relative;
  align-items: center;
}

.menu {
  display: flex;
  flex: 1 1;
  justify-content: flex-end;
}

.menu-icon {
  font-size: 24px;
}

.mobile-menu {
  display: none;
}

.web-menu {
  display: flex;
}

.logo {
  display: none;
  font-size: 36px;
  color: blueviolet;
  font-weight: 800;
}

@media only screen and (max-width: 720px) {
  .mobile-menu {
    display: flex;
  }

  .web-menu {
    display: none;
  }

  .logo {
    font-size: 24px;
  }
}

.mobile {
  background-color: #fff;
  height: 100vh;
  width: 60vw;
  position: absolute;
  right: 0px;
  top: -6px;
  padding: 36px 12px;
  display: flex;
  flex-direction: column;
}

.close-icon {
  display: flex;
  justify-content: flex-end;
  font-size: 24px;
}

.mobile-options {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 24px;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.mobile-option {
  display: flex;
  font-size: 24px;
  margin: 12px 0px;
}

.mobile-option a {
  text-decoration: none;
  color: #000;
  display: flex;
}

.web {
  display: flex;
  flex: 1 1;
  align-items: center;
}

.web-option {
  font-size: 20px;
  margin-right: 36px;
  font-weight: 700;
}

.web-option a {
  text-decoration: none;
  color: #000;
  display: flex;
  align-items: center;
}

.option-icon {
  display: flex;
  align-items: center;
  margin-right: 5px;
}

.web-option:last-child {
  margin-right: 0px;
}

html {
  scroll-behavior: smooth;
}

.home {
  display: flex;
  flex-direction: column;
  width: 70%;
  margin: auto;
}

@media only screen and (max-width: 1024px) {
  .home {
    width: 90%;
  }
}
@media only screen and (max-width: 512px) {
  .home {
    width: 100%;
  }
}

